.bdr-r {
  position: relative;
}

.bdr-r::after {
  border-right: 1px solid #e1e1e1;
  content: "";
  height: 100%;
  position: absolute;
  right: -1px;
  top: 0;
  transform: scaleX(0.5);
  width: 1px;
  z-index: 1;
}

.bdr-l {
  position: relative;
}

.bdr-l::before {
  border-left: 1px solid #e1e1e1;
  content: "";
  height: 100%;
  position: absolute;
  left: -1px;
  top: 0;
  transform: scaleX(0.5);
  width: 1px;
  z-index: 1;
}

.bdr-bottom {
  position: relative;
}

.bdr-bottom::after {
  border-bottom: 1px solid #e1e1e1;
  bottom: 0;
  content: "";
  height: 1px;
  position: absolute;
  right: 0;
  transform: scaleY(0.5);
  width: 100%;
  z-index: 1;
}

.bdr-top {
  position: relative;
}

.bdr-top:before {
  height: 1px;
  content: '';
  width: 100%;
  border-bottom: 1px solid #e1e1e1;
  position: absolute;
  top: -1px;
  right: 0;
  transform: scaleY(0.5);
  z-index: 1;
}

.img-item-thumb:after {
  position: absolute;
  top: -0.5px;
  left: -0.5px;
  width: 200%;
  height: 200%;
  content: '';
  border: 1px solid #e3e5e9;
  border-radius: 6px;
  transform: scale(0.5);
  transform-origin: top left;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5) {
  .bdr-r::after {
    transform: scaleX(0.7);
  }
  .bdr-bottom::after {
    transform: scaleY(0.7);
  }
  .bdr-top:before {
    transform: scaleY(0.7);
  }
}

@media only screen and (-webkit-min-device-pixel-ratio: 2) {
  .bdr-r::after {
    transform: scaleX(0.5);
  }
  .bdr-bottom::after {
    transform: scaleY(0.5);
  }
  .bdr-top:before {
    transform: scaleY(0.5);
  }
}

@media only screen and (-webkit-min-device-pixel-ratio: 3) {
  .bdr-r::after {
    transform: scaleX(0.333);
  }
  .bdr-bottom::after {
    transform: scaleY(0.333);
  }
  .bdr-top:before {
    transform: scaleY(0.333);
  }
}

